 break-word;\n  max-height: 200px;\n  overflow-y: auto;\n}\n\n.glide-diff__del {\n  background: #FFEEF0;\n  color: #B31D28;\n  text-decoration: line-through;\n  text-decoration-color: rgba(179, 29, 40, 0.4);\n  border-radius: 2px;\n  padding: 0 1px;\n}\n\n.glide-diff__ins {\n  background: #E6FFEC;\n  color: #22863A;\n  border-radius: 2px;\n  padding: 0 1px;\n}\n\n/* ================================================================\n   Checklist  dynamically injected action items\n   ================================================================ */\n\n.glide-task-list {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.glide-task-item {\n  display: flex;\n  gap: var(--spacing-sm);\n  padding: var(--spacing-sm) 10px;\n  background: var(--color-glide-bg-panel);\n  border: 1px solid var(--color-glide-border-light);\n  border-radius: var(--radius-sm);\n  transition: border-color var(--duration-glide) var(--ease-glide);\n}\n\n.glide-task-item:hover {\n  border-color: var(--color-glide-border);\n}\n\n.glide-task-item__check {\n  flex-shrink: 0;\n  display: flex;\n  align-items: flex-start;\n  padding-top: 1px;\n  cursor: pointer;\n}\n\n.glide-task-item__check input[type=\"checkbox\"] {\n  width: 16px;\n  height: 16px;\n  accent-color: var(--color-glide-blue);\n  cursor: pointer;\n}\n\n.glide-task-item__body {\n  flex: 1;\n  min-width: 0;\n}\n\n.glide-task-item__text {\n  font-size: var(--text-md);\n  line-height: 1.5;\n  color: var(--color-glide-text);\n}\n\n.glide-task-item__meta {\n  display: flex;\n  gap: var(--spacing-md);\n  flex-wrap: wrap;\n  margin-top: 2px;\n  font-size: var(--text-xs);\n  color: var(--color-glide-text-tertiary);\n}\n\n.glide-task-item__owner strong,\n.glide-task-item__deadline strong {\n  font-weight: 600;\n  color: var(--color-glide-text-secondary);\n}\n\n.glide-task-item:has(input[type=\"checkbox\"]:checked) .glide-task-item__text {\n  text-decoration: line-through;\n  color: var(--color-glide-text-tertiary);\n}\n\n/* ================================================================\n   Translation  dynamically injected side-by-side panels\n   ================================================================ */\n\n.glide-translate-view {\n  display: flex;\n  flex-direction: column;\n  gap: var(--spacing-sm);\n}\n\n.glide-translate-panel {\n  background: var(--color-glide-bg-panel);\n  border: 1px solid var(--color-glide-border-light);\n  border-radius: var(--radius-sm);\n  overflow: hidden;\n}\n\n.glide-translate-panel__label {\n  padding: 6px 10px;\n  font-size: var(--text-xs);\n  font-weight: 600;\n  color: var(--color-glide-text-secondary);\n  background: var(--color-glide-bg);\n  border-bottom: 1px solid var(--color-glide-border-light);\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n.glide-translate-panel__text {\n  padding: 10px;\n  font-size: var(--text-md);\n  line-height: 1.6;\n  color: var(--color-glide-text);\n  max-height: 240px;\n  overflow-y: auto;\n}\n\n.glide-translate-panel--target {\n  border-color: rgba(59, 158, 255, 0.2);\n}\n\n.glide-translate-panel--target .glide-translate-panel__label {\n  color: var(--color-glide-blue);\n  background: var(--color-glide-blue-light);\n}\n\n/* ================================================================\n   Spinner keyframes\n   ================================================================ */\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}\n", "",{"version":3,"sources":["webpack://./src/taskpane/taskpane.css"],"names":[],"mappings":"AAAA;;;;;;;;EAQE;;AAEF;;qEAEqE;;AAErE;EACE,UAAU;AACZ;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,qCAAqC;EACrC,kBAAkB;AACpB;;AAEA;EACE,4CAA4C;AAC9C;;AAEA;;qEAEqE;;AAErE;EACE,oBAAoB;EACpB,mBAAmB;EACnB,sBAAsB;EACtB,4CAA4C;EAC5C,8BAA8B;EAC9B,yBAAyB;EACzB,gBAAgB;EAChB,wCAAwC;EACxC,uBAAuB;EACvB,YAAY;EACZ,oCAAoC;EACpC,eAAe;EACf;kEACgE;EAChE,mBAAmB;AACrB;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;EAC9B,4CAA4C;AAC9C;;AAEA;EACE,0BAA0B;EAC1B,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;;qEA